"use client";

import React from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import analytics from "@/data/mock-analytics.json";
import { Analytics, YearData } from "@/types/analytics";

const typedAnalytics = analytics as Analytics;
const years = ["2020", "2021", "2022", "2023", "2024", "2025"];
const quarters = ["Q1", "Q2", "Q3", "Q4"] as const;

export default function AnalyticsDashboard() {
  const downloadPDF = () => {
    console.log("Downloading PDF report...");
  };

  const calculateYearlyTotal = (year: string) => {
    const yearData = typedAnalytics.visitorStats[year];
    return Object.values(yearData).reduce(
      (acc, quarter) => ({
        domestic: acc.domestic + quarter.domestic,
        international: acc.international + quarter.international,
        revenue: acc.revenue + quarter.revenue,
      }),
      { domestic: 0, international: 0, revenue: 0 }
    );
  };

  return (
    <div className="w-full space-y-4">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Tourism Analytics Dashboard</h2>
        <Button onClick={downloadPDF}>Download PDF Report</Button>
      </div>

      <Tabs defaultValue="visitors" className="w-full">
        <TabsList className="grid w-full grid-cols-2 lg:grid-cols-3">
          <TabsTrigger value="visitors">Visitor Statistics</TabsTrigger>
          <TabsTrigger value="revenue">Revenue Analysis</TabsTrigger>
          <TabsTrigger value="trends">Trends</TabsTrigger>
        </TabsList>

        <TabsContent value="visitors" className="mt-4">
          <div className="grid gap-4 md:grid-cols-2">
            {years.map((year) => {
              const totals = calculateYearlyTotal(year);
              return (
                <Card key={year}>
                  <CardHeader>
                    <CardTitle>{year} Visitors</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-2">
                      <div className="flex justify-between">
                        <span>Domestic:</span>
                        <span>{totals.domestic.toLocaleString()}</span>
                      </div>
                      <div className="flex justify-between">
                        <span>International:</span>
                        <span>{totals.international.toLocaleString()}</span>
                      </div>
                      <div className="flex justify-between font-bold">
                        <span>Total:</span>
                        <span>
                          {(totals.domestic + totals.international).toLocaleString()}
                        </span>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        </TabsContent>

        <TabsContent value="revenue" className="mt-4">
          <div className="grid gap-4 md:grid-cols-2">
            {years.map((year) => {
              const totals = calculateYearlyTotal(year);
              return (
                <Card key={year}>
                  <CardHeader>
                    <CardTitle>{year} Revenue</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-4">
                      <div className="text-2xl font-bold">
                        RM {totals.revenue.toLocaleString()}
                      </div>
                      <div className="space-y-2">
                        {quarters.map((quarter) => {
                          const quarterData = typedAnalytics.visitorStats[year][quarter];
                          return (
                            <div key={quarter} className="flex justify-between">
                              <span>{quarter}:</span>
                              <span>
                                RM {quarterData.revenue.toLocaleString()}
                              </span>
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        </TabsContent>

        <TabsContent value="trends" className="mt-4">
          <Card>
            <CardHeader>
              <CardTitle>Tourism Growth Trends</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-8">
                {Object.entries(typedAnalytics.clusterPerformance).map(([cluster, data]) => (
                  <div key={cluster} className="space-y-2">
                    <div className="flex justify-between items-center">
                      <span className="font-semibold capitalize">
                        {cluster.replace("-", " ")}
                      </span>
                      <span>
                        Revenue: RM {data.revenue.toLocaleString()}
                      </span>
                    </div>
                    <div className="h-2 bg-gray-100 rounded-full">
                      <div
                        className="h-full bg-black rounded-full"
                        style={{
                          width: `${(data.satisfaction / 5) * 100}%`,
                        }}
                      />
                    </div>
                    <div className="text-sm text-gray-600">
                      Satisfaction Score: {data.satisfaction}/5
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
