"use client";

import React, { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Calendar } from "@/components/ui/calendar";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";

// Mock events data - in real app, this would come from MTCP data
const mockEvents = {
  "2024-01": [
    { id: 1, title: "Sarawak Cultural Festival", date: "2024-01-15", type: "cultural" },
    { id: 2, title: "Food and Craft Fair", date: "2024-01-20", type: "food" },
  ],
  "2024-02": [
    { id: 3, title: "Traditional Music Concert", date: "2024-02-05", type: "arts" },
    { id: 4, title: "Heritage Walk", date: "2024-02-12", type: "heritage" },
  ],
};

type EventType = {
  id: number;
  title: string;
  date: string;
  type: string;
};

export default function EventsCalendar() {
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [selectedEvents, setSelectedEvents] = useState<EventType[]>([]);

  // Function to get events for the selected date
  const getEventsForDate = (date: Date) => {
    const monthKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}`;
    return mockEvents[monthKey as keyof typeof mockEvents] || [];
  };

  // Update events when date changes
  const handleDateSelect = (newDate: Date | undefined) => {
    setDate(newDate);
    if (newDate) {
      setSelectedEvents(getEventsForDate(newDate));
    }
  };

  return (
    <div className="w-full space-y-4">
      <h2 className="text-2xl font-bold">Tourism Events Calendar</h2>

      <Tabs defaultValue="calendar" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="calendar">Calendar View</TabsTrigger>
          <TabsTrigger value="list">List View</TabsTrigger>
        </TabsList>

        <TabsContent value="calendar" className="mt-4">
          <div className="grid gap-4 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle>Select Date</CardTitle>
              </CardHeader>
              <CardContent>
                <Calendar
                  mode="single"
                  selected={date}
                  onSelect={handleDateSelect}
                  className="rounded-md border"
                />
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Events</CardTitle>
              </CardHeader>
              <CardContent>
                {selectedEvents.length > 0 ? (
                  <div className="space-y-4">
                    {selectedEvents.map((event) => (
                      <div
                        key={event.id}
                        className="flex items-center justify-between border-b pb-2"
                      >
                        <div>
                          <h3 className="font-medium">{event.title}</h3>
                          <p className="text-sm text-gray-600">{event.date}</p>
                        </div>
                        <Badge>{event.type}</Badge>
                      </div>
                    ))}
                  </div>
                ) : (
                  <p className="text-gray-600">No events scheduled for this date.</p>
                )}
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="list" className="mt-4">
          <Card>
            <CardHeader>
              <CardTitle>Upcoming Events</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {Object.values(mockEvents)
                  .flat()
                  .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime())
                  .map((event) => (
                    <div
                      key={event.id}
                      className="flex items-center justify-between border-b pb-2"
                    >
                      <div>
                        <h3 className="font-medium">{event.title}</h3>
                        <p className="text-sm text-gray-600">{event.date}</p>
                      </div>
                      <Badge>{event.type}</Badge>
                    </div>
                  ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
