"use client";

import React, { useState, useEffect } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import TourismClusters from "@/components/TourismClusters";
import GrantApplication from "@/components/GrantApplication";
import AnalyticsDashboard from "@/components/AnalyticsDashboard";
import EventsCalendar from "@/components/EventsCalendar";
import { initializePWA, checkOnlineStatus } from "@/lib/pwa";

const languages = [
  { code: "en", name: "English" },
  { code: "ms", name: "Bahasa Melayu" },
  { code: "zh", name: "中文" },
];

export default function HomePage() {
  const [currentLang, setCurrentLang] = useState("en");
  const [isOnline, setIsOnline] = useState(true);
  const [canInstall, setCanInstall] = useState(false);

  useEffect(() => {
    // Initialize PWA
    const pwa = initializePWA();
    if (pwa) {
      setCanInstall(true);
    }

    // Check online status
    const cleanup = checkOnlineStatus((status) => {
      setIsOnline(status);
    });

    return () => {
      cleanup();
    };
  }, []);

  const handleInstallClick = () => {
    const pwa = initializePWA();
    if (pwa) {
      pwa.promptInstall();
    }
  };

  return (
    <main className="min-h-screen bg-white text-black">
      <header className="border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex justify-between items-center">
            <h1 className="text-2xl font-bold">INTOURCAMS</h1>
            <div className="flex items-center gap-4">
              <div className="flex gap-2">
                {languages.map((lang) => (
                  <Button
                    key={lang.code}
                    variant={currentLang === lang.code ? "default" : "outline"}
                    onClick={() => setCurrentLang(lang.code)}
                    className="min-w-[80px]"
                  >
                    {lang.name}
                  </Button>
                ))}
              </div>
              {canInstall && (
                <Button onClick={handleInstallClick} variant="outline">
                  Install App
                </Button>
              )}
            </div>
          </div>
        </div>
      </header>

      {!isOnline && (
        <div className="bg-yellow-100 p-2 text-center">
          <p className="text-yellow-800">
            You are currently offline. Some features may be limited, but you can still access cached content.
          </p>
        </div>
      )}

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <Tabs defaultValue="clusters" className="space-y-8">
          <TabsList className="grid grid-cols-2 lg:grid-cols-4 gap-2">
            <TabsTrigger value="clusters">Tourism Clusters</TabsTrigger>
            <TabsTrigger value="grants">Grant Applications</TabsTrigger>
            <TabsTrigger value="analytics">Analytics</TabsTrigger>
            <TabsTrigger value="events">Events Calendar</TabsTrigger>
          </TabsList>

          <TabsContent value="clusters" className="space-y-8">
            <TourismClusters />
          </TabsContent>

          <TabsContent value="grants" className="space-y-8">
            <GrantApplication />
          </TabsContent>

          <TabsContent value="analytics" className="space-y-8">
            <AnalyticsDashboard />
          </TabsContent>

          <TabsContent value="events" className="space-y-8">
            <EventsCalendar />
          </TabsContent>
        </Tabs>
      </div>

      <footer className="border-t mt-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <p className="text-center text-gray-600">
            © 2024 INTOURCAMS - Integrated Tourism Coordination and Monitoring System
          </p>
          <p className="text-center text-sm text-gray-500 mt-2">
            {isOnline ? "Connected to network" : "Working offline"}
          </p>
        </div>
      </footer>
    </main>
  );
}
